/**
* Tooltips
*/

@mixin spaceTop() {
  transform: translate3d(0, 6px, 0);
}

@mixin spaceBottom() {
  transform: translate3d(0, -3px, 0);
}

// Global Tooltip
.tooltip-container {
  z-index: $indexTooltip;
  pointer-events: none;
  display: inline-block;
  opacity: 0;
  position: absolute;
  color: #fff;
  border-radius: $globalRadius;
  padding: 7px 10px 7px;
  will-change: transform, opacity;
  transition: opacity .4s ease, transform .4s ease;
  font-size: $smallFontSize;
  font-weight: bold;
  transform: translate3d(0, -6px, 0);
  background: $tooltipBg;
  word-wrap: break-word;

  &.error-tooltip {
    word-break: normal;
  }
  &.open {
    opacity: 1;
  }
  max-width: 250px;
  text-align: center;

  &.open {
    // normal state (centered)
    &.is-collision-bottom:not(.is-collision-left) {
      @include spaceBottom();
      @include arrow-bottom($tooltipBg);
    }

    &:not(.is-collision-bottom):not(.is-collision-left) {
      @include spaceTop();
      @include arrow-top($tooltipBg);
    }

    // left collisions
    &.is-collision-left.is-collision-bottom {
      @include spaceBottom();
      @include arrow-bottom($tooltipBg, 15px);
    }

    &.is-collision-left:not(.is-collision-bottom) {
      @include spaceTop();
      @include arrow-top($tooltipBg, 15px);
    }

    // right collisions
    &.is-collision-right:not(.is-collision-bottom) {
      @include spaceTop();
      @include arrow-top($tooltipBg, 93%);
    }

    &.is-collision-right.is-collision-bottom {
      @include spaceBottom();
      @include arrow-bottom($tooltipBg, 93%);
    }
  }
}